import React from 'react';
class Modal extends React.Component{
    render(){
        return(
            <div className='modal'>
                <div className='modal__body'>
                    <button className='modal__add'>+</button>
                    <button className='modal__close'>+</button>
                    <div className='modal__content'>
                        <input type='text' clasName='modal__input'/>
                        <div className="modal-marks_default">
                            <div className="mark mark_default">Продукты</div>
                        </div>
                        <div className="modal-marks_undefault">
                            <div className="mark mark_undefault">Продукты</div>
                            <div className="add-mark">
                                <div className="add-mark__label">+</div>
                                <input className="add-mark__input"/>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        )
    }
}

export default Modal;


